export default {
	data() {
		return {
			load: true
		};
	},
	methods: {
		navUrl(e) {
			switch (e) {
				case 1:
					uni.navigateTo({
						url: '/pages/index/collect'
					});
					break;
				case 2:
					uni.navigateTo({
						url: '/pages/index/newback'
					});
					break;
				case 3:
					uni.navigateTo({
						url: '/pages/index/recommed'
					});
					break;
				case 4:
					uni.makePhoneCall({
						phoneNumber: this.data.phone
					});
					break;
				case 5:
					uni.switchTab({
						url: '/pages/tabar/classify'
					});
					break;
				case 6:
					uni.switchTab({
						url: '/pages/tabar/shopcart'
					});
					break;
				case 7:
					uni.switchTab({
						url: '/pages/tabar/order'
					});
					break;
				case 8:
					uni.switchTab({
						url: '/pages/tabar/user'
					});
					break;
				default:
					app.globalData.classId = cate_id;
					wx.switchTab({
						url: '/pages/tabar/classify'
					});
					break;
			}
		},
		openCart() {
			uni.hideTabBar();
			this.$refs.popup.open();
		},
		onClose() {
			this.$refs.popup.close();
			uni.showTabBar();
		},
		shopDetailPage() {
			uni.navigateTo({
				url: '/pages/index/shopdetail'
			});
		}
	}
};
